export default {
	data() {
		return {
			pageCur: 'homepage',
		}
	},
	methods: {
		NavChange: function(e) {
			this.pageCur = e.currentTarget.dataset.cur;
		},
		CommitEticket: function(e) {
			let user = uni.getStorageSync('user');
			if (user) {
				uni.navigateTo({
					url: '../commiteticket/commiteticket'
				});
			} else {
			    uni.showModal({
			    	content: '请登录后使用该功能~',
			    	showCancel: false
			    });	
			}
		}
	}
}
